}

.gallery__img--second {
  animation-delay: 5s;
}

.gallery__img--third {
  animation-delay: 10s;
}

@keyframes changeGallery {
  0% {
    opacity: 1;
  }

  20% {
    opacity: 1;
  }

  33% {
    opacity: 0;
  }

  87% {
    opacity: 0;
  }

  100% {
    opacity: 1;
